@import url(https://fonts.googleapis.com/css?family=Heebo:100,300,400,500,700,900" rel="stylesheet);
@import url(https://use.fontawesome.com/releases/v5.8.1/css/all.css);
body {
  font-family: 'Heebo', sans-serif; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

.mr-bt {
  margin-bottom: 20px; }

.profile-button {
  background: -webkit-gradient(linear, left top, right top, from(#DA4453), to(#89216B));
  background: -webkit-linear-gradient(left, #DA4453, #89216B);
  background: linear-gradient(to right, #DA4453, #89216B);
  padding: 8px 30px;
  margin-right: 10px;
  color: #ffff;
  border-radius: 50px;
  font-family: 'Heebo',sans-serif;
  display: inline-block;
  -webkit-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in; }
  .profile-button:hover {
    background: -webkit-gradient(linear, left top, right top, from(#89216B), to(#DA4453));
    background: -webkit-linear-gradient(left, #89216B, #DA4453);
    background: linear-gradient(to right, #89216B, #DA4453);
    text-decoration: none;
    color: #fff; }

hr {
  width: 200px;
  border: 0.5px solid #AE3160;
  margin-left: 0; }

.pad-none {
  padding-left: 0;
  padding-right: 0; }

.projects-wrap {
  padding: 0; }
  .projects-wrap .work-box {
    background: no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    text-align: left;
    position: relative; }
    .projects-wrap .work-box h2 {
      font-size: 26pt;
      font-weight: 900;
      color: #fff;
      padding-top: 40px;
      padding-left: 40px;
      line-height: 0.5; }
    .projects-wrap .work-box span {
      font-size: 16pt;
      color: #fff;
      font-weight: 100;
      padding-left: 40px;
      -webkit-font-smoothing: antialiased; }
    .projects-wrap .work-box div img {
      padding-top: 50px; }
    .projects-wrap .work-box a img {
      position: absolute;
      right: 40px;
      bottom: 40px;
      -webkit-transition: all 0.3s ease-in;
      transition: all 0.3s ease-in; }
      .projects-wrap .work-box a img:hover {
        -webkit-transform: scale(1.1);
                transform: scale(1.1); }

.cont-wrap {
  display: flex;
  align-items: center;
  justify-content: left;
  padding-left: 65px;
  height: 100vh;
  position: relative; }
  .cont-wrap .profile-box {
    position: fixed; }
  .cont-wrap h1 {
    font-family: 'Heebo',sans-serif;
    font-weight: 900;
    font-size: 36px;
    line-height: 1; }
  .cont-wrap span {
    font-family: 'Heebo', sans-serif;
    font-weight: 300;
    font-size: 20px;
    line-height: 1;
    -webkit-font-smoothing: antialiased; }
  .cont-wrap .social a {
    margin-right: 20px;
    padding: 40px 0px; }
    .cont-wrap .social a img {
      -webkit-filter: grayscale(100%);
      /* Safari 6.0 - 9.0 */
      filter: grayscale(100%); }
      .cont-wrap .social a img:hover {
        -webkit-filter: grayscale(0);
        /* Safari 6.0 - 9.0 */
        filter: grayscale(0); }

.img-wrap {
  position: relative; }
  .img-wrap .color-bg {
    background-image: -webkit-gradient(linear, left top, right top, from(#DA4453), to(#89216B));
    background-image: -webkit-linear-gradient(left, #DA4453, #89216B);
    background-image: linear-gradient(to right, #DA4453, #89216B);
    height: 100vh; }
  .img-wrap .img-bg {
    position: absolute;
    top: 70px;
    right: -100px;
    box-shadow: -4px 3px 12px -3px rgba(0, 0, 0, 0.75);
    z-index: 1; }

.skill-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff; }
  .skill-wrap .content-box .title {
    font-size: 36px;
    font-family: 'Heebo', sans-serif;
    font-weight: 900;
    background: -webkit-linear-gradient(right, #DA4453, #89216B);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .skill-wrap .content-box h3 {
    font-size: 28px;
    font-weight: 900;
    padding-top: 20px; }
  .skill-wrap .content-box hr {
    width: 70px;
    margin-top: -10px;
    margin-bottom: 20px; }
  .skill-wrap .content-box ul {
    list-style-type: none;
    padding-left: 0;
    padding-right: 20px; }
    .skill-wrap .content-box ul li {
      line-height: 1.7; }
      .skill-wrap .content-box ul li .text {
        width: 170px;
        display: inline-block;
        font-size: 14pt; }
      .skill-wrap .content-box ul li .rating-wrap {
        display: inline; }
        .skill-wrap .content-box ul li .rating-wrap .rating {
          background: -webkit-linear-gradient(right, #DA4453, #89216B);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          font-size: 20pt; }
  .skill-wrap footer {
    position: absolute;
    bottom: 20px;
    right: 20px; }

@media (min-width: 1600px) and (max-width: 1920px) {
  .projects-wrap div .project-img {
    width: 900px; }
  .img-bg {
    position: absolute;
    top: 150px !important; } }

@media (min-width: 768px) and (max-width: 1024px) {
  .projects-wrap .work-box h2 {
    padding-top: 70px; }
  .projects-wrap div .project-img {
    width: 600px;
    padding-top: 150px !important; }
  .img-bg {
    display: none; }
  .skill-wrap {
    padding-top: 50px;
    padding-left: 20px; } }

@media (min-width: 320px) and (max-width: 480px) {
  .projects-wrap .work-box h2 {
    padding-top: 70px; }
  .projects-wrap div .project-img {
    width: 330px;
    padding-top: 100px !important; }
  .img-bg {
    position: static !important;
    height: 300px;
    overflow: hidden; }
  .skill-wrap {
    padding-top: 50px;
    padding-left: 20px; }
    .skill-wrap footer {
      bottom: -30px;
      left: -165px; }
  .cont-wrap .profile-box {
    position: relative !important; } }

