@import url('https://fonts.googleapis.com/css?family=Heebo:100,300,400,500,700,900" rel="stylesheet');
@import url('https://use.fontawesome.com/releases/v5.8.1/css/all.css');

body{
  font-family: 'Heebo', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.mr-bt {
  margin-bottom: 20px;
}

.profile-button {
  background: linear-gradient(to right, #DA4453, #89216B);
  padding: 8px 30px;
  margin-right: 10px;
  color: #ffff;
  border-radius: 50px;
  font-family: 'Heebo',sans-serif;
  display: inline-block;
  transition: all 0.5s ease-in;

  &:hover {
    background: linear-gradient(to right, #89216B, #DA4453);
    text-decoration: none;
    color: #fff;
  }
}

hr {
  width: 200px;
  border: 0.5px solid #AE3160;
  margin-left: 0;
}

.pad-none {
  padding-left: 0;
  padding-right:0;
}


.projects-wrap {
   padding: 0;

   .work-box {
     background: no-repeat;
     background-size: cover;
     background-position: center;
     height: 100vh;
     text-align: left;
     position: relative;

     h2 {
      font-size: 26pt;
      font-weight: 900;
      color: #fff;
      padding-top: 40px;
      padding-left: 40px;
      line-height: 0.5;

    }
    span {
      font-size: 16pt;
      color: #fff;
      font-weight: 100;
      padding-left: 40px;
      -webkit-font-smoothing: antialiased;
    }
    div {
      img {
        padding-top: 50px;
      }
    }
    a {
      img {
        position: absolute;
        right: 40px;
        bottom: 40px;
        transition: all 0.3s ease-in;
        &:hover {transform: scale(1.1)}
      }
    }
   }
}

.cont-wrap {
  display: flex;
  align-items: center;
  justify-content: left;
  padding-left: 65px;
  height: 100vh;
  position: relative;
 

    .profile-box {
      position: fixed;
    }

    h1 {
        font-family: 'Heebo',sans-serif; 
        font-weight: 900;
        font-size: 36px;
        line-height: 1;
       }

    span {
        font-family: 'Heebo', sans-serif;
        font-weight: 300;
        font-size: 20px;
        line-height: 1;
        -webkit-font-smoothing: antialiased;
        }

    .social {
        a {
          margin-right: 20px;
          padding: 40px 0px;
          img {
            -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
            filter: grayscale(100%);

            &:hover {
              -webkit-filter: grayscale(0); /* Safari 6.0 - 9.0 */
              filter: grayscale(0);
            }

          }
        }
    }
}

.img-wrap {
  position: relative;
  .color-bg {
    background-image: linear-gradient(to right, #DA4453, #89216B);
  height: 100vh;
  }
  .img-bg {
    position: absolute;
    top: 70px;
    right: -100px;
    -webkit-box-shadow: -4px 3px 12px -3px rgba(0,0,0,0.75);
-moz-box-shadow: -4px 3px 12px -3px rgba(0,0,0,0.75);
box-shadow: -4px 3px 12px -3px rgba(0,0,0,0.75);
z-index: 1;  }
}
 
.skill-wrap { display: flex; align-items: center; justify-content: center;
  background-color: #fff;
  .content-box {
    .title {
      font-size: 36px;
      font-family: 'Heebo', sans-serif;
      font-weight: 900;
      background: -webkit-linear-gradient(right, #DA4453, #89216B);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    h3 {
      font-size: 28px;
      font-weight: 900;
      padding-top: 20px;
    }
    hr {
      width: 70px;
      margin-top: -10px;
      margin-bottom: 20px;
    }
    ul {
      list-style-type: none;
      padding-left: 0;
      padding-right: 20px;
      li {
        line-height: 1.7;
        .text {
          width: 170px;
          display: inline-block;
          font-size: 14pt;
        }
        .rating-wrap {
          display: inline;
          .rating{
            background: -webkit-linear-gradient(right, #DA4453, #89216B);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-size: 20pt;
          }
        }
      }
    }
  }
  footer {
    position: absolute;
    bottom: 20px;
    right: 20px;
  }
}


@media (min-width: 1600px) and (max-width: 1920px) {
  .projects-wrap {

    div {
      .project-img {
        width: 900px;
            }
    }
  }

  .img-bg {
    position: absolute;
    top: 150px!important;
  }

}


@media (min-width: 768px) and (max-width: 1024px) {
  .projects-wrap {
    .work-box {
      h2{
        padding-top: 70px;
      }
    }
    div {
      .project-img {
        width: 600px;
        padding-top: 150px!important;
            }
    }
  }
  .img-bg {
    display: none;   
  }
  .skill-wrap {padding-top: 50px; padding-left: 20px;}


}

@media (min-width: 320px) and (max-width: 480px) {
  .projects-wrap {
    .work-box {
      h2{
        padding-top: 70px;
      }
    }
    div {
      .project-img {
        width: 330px;
        padding-top: 100px!important;
            }
    }
  }
  .img-bg {
    position: static!important;
    height: 300px;    
    overflow: hidden;
  }
  .skill-wrap {padding-top: 50px; padding-left: 20px;
  footer {
    bottom: -30px;
    left: -165px;
  }
  }
  .cont-wrap {
  .profile-box {
    position: relative!important;
  }
}

}